import { Injectable } from '@angular/core';
import { SFButton } from './interface';
import { SFUISchemaItem } from './schema/ui';

@Injectable({ providedIn: 'root' })
export class DelonFormConfig {
  /**
   *  `ERRORSDEFAULT``[ 'type', 'enum' ]`
   *
   * - `type`  Schema  `type` 
   * - `enum` 
   */
  ingoreKeywords?: string[] = ['type', 'enum'];
  /**
   * [ajv](http://epoberezkin.github.io/ajv/#options) 
   */
  ajv?: any;
  /**
   * `true`
   * - `true` 
   * - `false` 
   */
  liveValidate?: boolean = true;
  /**
   *  `autocomplete` `on`
   */
  autocomplete?: 'on' | 'off' | null = null;
  /**
   * `false`
   */
  firstVisual?: boolean = false;
  /**
   * `false`
   */
  onlyVisual?: boolean = false;
  /**
   * 
   */
  errors?: { [key: string]: string } = {};
  /**
   * 
   */
  ui?: SFUISchemaItem;
  /**
   *  `nzSize` 
   */
  size?: 'default' | 'large' | 'small';
  /**
   * 
   */
  button?: SFButton = {
    submit_type: 'primary',
    reset_type: 'default',
  };
  /**
   * date`type="string"`  `schema.format`  `ui.format` `yyyy-MM-dd HH:mm:ss`
   */
  uiDateStringFormat?: string = 'yyyy-MM-dd HH:mm:ss';
  /**
   * date`type="number"`  `schema.format`  `ui.format` `x` 13Unix Timestamp
   */
  uiDateNumberFormat?: string = 'x';
  /**
   * time`type="string"`  `schema.format`  `ui.format` `HH:mm:ss`
   */
  uiTimeStringFormat?: string = 'HH:mm:ss';
  /**
   * time`type="number"`  `schema.format`  `ui.format` `x` 13Unix Timestamp `1970-01-01`
   */
  uiTimeNumberFormat?: string = 'x';
  /**
   *  `format: 'email'` Email
   */
  uiEmailSuffixes?: string[] = ['qq.com', '163.com', 'gmail.com', '126.com', 'aliyun.com'];
}
